<template>
    <section>
        <el-row :gutter="10">
            <el-col :span="16">
                <div class="siteshortnamediv">{{ siteshortname }}</div>
                <div id="mapGl" class="map"></div
            ></el-col>
            <el-col :span="8">
                <el-tabs
                    v-model="activeTab"
                    type="border-card"
                    @tab-click="tabClick"
                >
                    <el-tab-pane label="平面图" name="plane">
                        <el-alert
                            title="1. 上传平面图"
                            type="warning"
                            :closable="false"
                            description="只能上传jpg文件，且不超过1MB"
                        >
                        </el-alert>
                        <el-form :model="imgForm" ref="imgForm">
                            <el-form-item label="" prop="Image">
                                <el-upload
                                    :class="uploadDisabled"
                                    ref="upload"
                                    action="1"
                                    :limit="1"
                                    :on-remove="handleRemove"
                                    accept=".jpg"
                                    :auto-upload="true"
                                    :http-request="fileUpload"
                                    :on-change="beforeAvatarUpload"
                                    list-type="picture-card"
                                    :file-list="planeurls"
                                >
                                    <!-- <el-button size="small" type="primary"
                                        >选择图片</el-button
                                    > -->
                                    <i slot="default" class="el-icon-plus"></i>
                                </el-upload>

                                <!-- <el-button
                                    v-show="!imgForm.Image"
                                    style="margin-left: 10px"
                                    size="small"
                                    type="success"
                                    @click="submitUpload"
                                    >上传到服务器</el-button
                                > -->
                            </el-form-item>
                        </el-form>
                        <!-- <el-dialog :visible.sync="dialogVisible">
                            <img width="100%" :src="dialogImageUrl" alt="" />
                        </el-dialog> -->
                        <el-form
                            ref="planeForm"
                            :model="mapData"
                            label-position="top"
                        >
                            <el-alert
                                title="2. 点击地图，设置平面图左上角和右下角经纬度"
                                type="warning"
                                description="拖拽标注，微调平面图位置"
                                :closable="false"
                                class="m-t-20"
                            >
                            </el-alert>
                            <el-row class="form-content">
                                <el-col :span="24">
                                    <el-form-item>
                                        <el-col :span="24" class="text-regular">
                                            平面图左上角经纬度
                                        </el-col>
                                        <el-col :span="12">
                                            <el-input
                                                v-model="mapData.ltlng"
                                            ></el-input>
                                        </el-col>
                                        <el-col :span="12">
                                            <el-input
                                                v-model="mapData.ltlat"
                                            ></el-input>
                                        </el-col>
                                    </el-form-item>
                                    <el-form-item>
                                        <el-col :span="24" class="text-regular">
                                            平面图右下角经纬度
                                        </el-col>
                                        <el-col :span="12">
                                            <el-input
                                                v-model="mapData.rblng"
                                            ></el-input>
                                        </el-col>
                                        <el-col :span="12">
                                            <el-input
                                                v-model="mapData.rblat"
                                            ></el-input>
                                        </el-col>
                                    </el-form-item>
                                </el-col>
                            </el-row>
                            <el-row type="flex" justify="end">
                                <el-form-item>
                                    <el-button size="medium">取消</el-button>
                                    <el-button
                                        type="primary"
                                        @click="savePlane"
                                        :loading="editLoading"
                                        size="medium"
                                        >保存</el-button
                                    >
                                </el-form-item>
                            </el-row>
                        </el-form>
                    </el-tab-pane>
                    <el-tab-pane label="建筑俯视" name="shape">
                        <div class="text-base text-regular" v-show="false">
                            调整平面图的透明度，避免勾画建筑的轮廓
                        </div>
                        <el-slider
                            v-model="planeOpacity"
                            show-input
                            @change="changeOpacity"
                            :max="1"
                            :step="0.1"
                            v-show="false"
                        >
                        </el-slider>
                        <el-divider>多边形列表</el-divider>
                        <div
                            ref="listDiv"
                            :style="`max-height: ${listHeight}px; overflow: auto`"
                        >
                            <el-row
                                :gutter="10"
                                class="shape-row"
                                v-for="(item, index) in shapeDatas"
                                :key="index"
                                @click.native="selectShape(index)"
                            >
                                <el-form :model="item" ref="shapeForm">
                                    <el-col :span="6">
                                        <el-form-item class="form-item">
                                            多边形{{ index + 1 }}
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="10">
                                        <!-- <el-form-item class="form-item">
                                            高度
                                            <div
                                                class="table-div"
                                                v-if="index != editShapeIndex"
                                            >
                                                {{ item.shapeheight }}
                                            </div>
                                            <div class="table-div" v-else>
                                                <el-input-number
                                                    v-model="item.shapeheight"
                                                    size="mini"
                                                    controls-position="right"
                                                    @change="changeHeight"
                                                    :min="0"
                                                    :max="100"
                                                    :step="10"
                                                ></el-input-number>
                                            </div>
                                        </el-form-item> -->
                                        <el-form-item class="form-item">
                                            <div
                                                class="table-div"
                                                v-if="index != editShapeIndex"
                                            >
                                                高度&nbsp;{{ item.shapeheight }}
                                            </div>
                                            <div class="table-div" v-else>
                                                <el-input
                                                    v-model="item.shapeheight"
                                                    ><template slot="prepend"
                                                        >高度</template
                                                    ></el-input
                                                >
                                            </div>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="8">
                                        <el-form-item class="form-item">
                                            <el-link
                                                type="primary"
                                                :underline="false"
                                                @click="editShape(index)"
                                                v-show="editShapeIndex == -1"
                                                >编辑</el-link
                                            >
                                            <el-link
                                                type="primary"
                                                :underline="false"
                                                @click="deleteShape(index)"
                                                v-show="editShapeIndex == -1"
                                                >删除</el-link
                                            >
                                        </el-form-item>
                                        <el-form-item
                                            class="form-item"
                                            v-show="editShapeIndex == index"
                                        >
                                            <el-button
                                                type="primary"
                                                @click="saveShape"
                                                :loading="editLoading"
                                                size="mini"
                                                plain
                                                >保存</el-button
                                            >
                                            <el-button
                                                size="mini"
                                                plain
                                                @click="cancleSaveShape"
                                                >取消</el-button
                                            >
                                        </el-form-item>
                                    </el-col>
                                </el-form>
                            </el-row>
                        </div>
                        <el-button
                            type="primary"
                            plain
                            @click="addShape"
                            size="small"
                            >创建多边形</el-button
                        >

                        <el-divider>展示视角</el-divider>
                        <el-alert
                            title="调整地图展示视角后保存"
                            type="warning"
                            description="在左侧地图上，长按鼠标滚轮，调整地图的展示角度；滚动鼠标滚轮，调整地图缩放比例。"
                            :closable="false"
                        >
                        </el-alert>
                        <el-button
                            type="primary"
                            plain
                            @click="saveMapView"
                            size="small"
                            >保存视角</el-button
                        >
                        <el-button
                            type="primary"
                            plain
                            @click="viewMap"
                            size="small"
                            v-show="mapData.mapzoom > 0"
                            >切换到展示视角</el-button
                        ></el-tab-pane
                    >
                    <el-tab-pane label="设备标注" name="equip">
                        <el-divider>设备标注列表</el-divider>
                        <div
                            ref="equipListDiv"
                            :style="`max-height: ${equipListHeight}px; overflow: auto`"
                        >
                            <el-row
                                :gutter="10"
                                class="shape-row"
                                v-for="(item, index) in equipDatas"
                                :key="index"
                                @click.native="selectEquip(index)"
                            >
                                <el-form
                                    :model="item"
                                    ref="equipForm"
                                    :rules="equipFormRules"
                                >
                                    <!-- <el-col :span="1">
                                        <el-form-item class="form-item">
                                            {{ index + 1 }}
                                        </el-form-item>
                                    </el-col> -->
                                    <el-col :span="11">
                                        <div
                                            class="table-div"
                                            v-if="index != editShapeIndex"
                                        >
                                            {{ mEquipType[item.equiptype] }}
                                            /<br />
                                            {{ item.equipcode }}
                                        </div>
                                        <el-form-item
                                            prop="equiptypecode"
                                            v-else
                                        >
                                            <el-cascader
                                                v-model="item.equiptypecode"
                                                :options="equipOpts"
                                                :props="{
                                                    expandTrigger: 'hover',
                                                }"
                                            ></el-cascader>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="7">
                                        <el-form-item class="form-item">
                                            <div
                                                class="table-div"
                                                v-if="index != editShapeIndex"
                                            >
                                                高度&nbsp;{{ item.equipheight }}
                                            </div>
                                            <div class="table-div" v-else>
                                                <el-input
                                                    v-model="item.equipheight"
                                                    ><template slot="prepend"
                                                        >高度</template
                                                    ></el-input
                                                >
                                            </div>
                                        </el-form-item>
                                    </el-col>
                                    <el-col
                                        :span="6"
                                        style="text-align: center"
                                    >
                                        <el-form-item class="form-item">
                                            <el-link
                                                type="primary"
                                                :underline="false"
                                                @click="editEquip(index)"
                                                v-show="editShapeIndex == -1"
                                                >编辑</el-link
                                            >
                                            <el-link
                                                type="primary"
                                                :underline="false"
                                                @click="deleteEquip(index)"
                                                v-show="editShapeIndex == -1"
                                                >删除</el-link
                                            >
                                        </el-form-item>
                                        <el-form-item
                                            class="form-item"
                                            v-show="editShapeIndex == index"
                                        >
                                            <el-link
                                                type="primary"
                                                :underline="false"
                                                @click.native="saveEquip"
                                                :loading="editLoading"
                                                >保存</el-link
                                            >
                                            <el-link
                                                type="primary"
                                                :underline="false"
                                                @click="cancleSaveEquip"
                                                >取消</el-link
                                            >
                                        </el-form-item>
                                    </el-col>
                                </el-form>
                            </el-row>
                        </div>
                        <el-button
                            type="primary"
                            plain
                            @click="addEquip"
                            size="small"
                            >创建设备标注</el-button
                        >
                    </el-tab-pane>
                </el-tabs>
            </el-col>
        </el-row>
    </section>
</template>
<script>
import {
    getSetting,
    addSiteMap,
    editSiteMap,
    getSiteMap,
    saveSiteMapPlane,
    deleteSiteMapPlane,
    getsite,
    addSiteMapShape,
    editSiteMapShape,
    getSiteMapShape,
    deleteSiteMapShape,
    getSiteMapEquip,
    addSiteMapEquip,
    editSiteMapEquip,
    deleteSiteMapEquip,
} from "../../api/api";
import * as mapvgl from "mapvgl";
import { BMPGL } from "../../../util/bmapgl";
// import Markmap from "../../components/MarkMap";
import dicmixin from "../../assets/dic/dicmixin";
import bgimg from "../../assets/c5e08f3a17fdf75b600169ad289d09e6.jpg";
import buildlight from "../../assets/buildlight.jpg";
import markerImg from "../../assets/eqp-marker.png";

export default {
    // components: { Markmap },
    // mixins: [dicmixin],
    data() {
        return {
            S034: "",
            ak: "YHMXpNzQSpxfmPCQxAjFcsLhHzGpGSKr",
            editLoading: false,
            sitelng: "",
            sitelat: "",
            clickIndex: 0,
            tabType: "plane",
            map: null,
            activeTab: "plane",
            shapeDatas: [],
            equipDatas: [],
            equipUnuse: [],
            equipOpts: [],
            planeurls: [],
            mEquipType: {
                1: "扬尘",
                2: "摄像头",
                3: "塔吊",
                4: "升降机",
                5: "卸料平台",
                6: "深基坑",
                7: "高支模",
                8: "临边防护",
                9: "实名制",
                10: "临边围挡",
            },
            // shapeOldDatas: [],
            shapes: [],
            equips: [],
            currShapeIndex: 0,
            editShapeIndex: -1,
            shapeLayer: null, //立体图层
            shapeLayerView: null, //立体图层
            shapeLayerData: [],
            mapType: "2D",
            listHeight: 0,
            equipListHeight: 0,
            mapData: {
                SMID: 0,
                SITEID: this.$route.params.id,
                GROUPID: 0,
                heading: 0,
                tilt: 0,
                ctlng: 0,
                ctlat: 0,
                mapzoom: 0,
                ltlng: 0,
                ltlat: 0,
                rblng: 0,
                rblat: 0,
                planeurl: "",
            },
            form: {},
            shapeform: {},
            imgForm: {},
            points: [],
            imgOverlay: null,
            planeOpacity: 0.5,
            stMarker: null,
            edMarker: null,
            // editPolygon: null,
            isEditShape: false,
            isEditEquip: false,
            editLoading: false,
            equipFormRules: {
                equiptypecode: [
                    {
                        required: true,
                        message: "请选择设备",
                        trigger: "blur",
                    },
                ],
            },
            // dialogImageUrl: "",
            // dialogVisible: false,
            // disabled: false,
            uploadDisabled: "",
            attachmentId: [], // 存储上传之后后台传过来的ID
            fileList: [],
            siteshortname: "",
        };
    },
    methods: {
        getSetting() {
            getSetting({ id: "S034" }).then((res) => {
                if (res.success) {
                    this.S034 = res.data.value;
                    this.getSite();
                }
            });
        },
        getSite() {
            getsite({ id: this.$route.params.id }).then((res) => {
                if (res.success) {
                    this.sitelng = res.data.sitelng;
                    this.sitelat = res.data.sitelat;
                    this.mapData.GROUPID = res.data.GROUPID;
                    this.siteshortname = res.data.siteshortname;
                    this.loadMap();
                    // let planeurl =
                    //     "http://" +
                    //     this.S034 +
                    //     "/resourse/" +
                    //     res.data.GROUPID +
                    //     "/Plane/tmb_" +
                    //     this.$route.params.id +
                    //     ".jpg";

                    // var ImgObj = new Image();
                    // ImgObj.src = planeurl;
                    // ImgObj.onerror = () => {
                    //     this.$refs.upload.clearFiles();
                    //     this.planeurls = [];
                    //     this.uploadDisabled = "";
                    // };
                    // ImgObj.onload = () => {
                    //     this.planeurls.push({
                    //         name: "plane.jpg",
                    //         url: planeurl,
                    //     });
                    //     this.uploadDisabled = "disabled";

                    //     this.loadMap();
                    // };
                }
            });
        },
        getSiteMapData() {
            getSiteMap({ SITEID: this.$route.params.id }).then((res) => {
                if (res.success && res.data) {
                    this.planeurls = [];
                    this.mapData = res.data;
                    this.setStEdMarker(1);
                    this.setStEdMarker(2);
                    if (this.mapData.planeurl) {
                        this.planeurls.push({
                            name: "plane.jpg",
                            url: this.mapData.planeurl,
                        });
                        this.uploadDisabled = "disabled";
                        this.showPlane();
                    } else {
                        this.$refs.upload.clearFiles();
                        this.uploadDisabled = "";
                        this.showPlane();
                    }
                }
            });
        },
        getSiteMapShapeData() {
            getSiteMapShape({ SITEID: this.$route.params.id }).then((res) => {
                if (res.success) {
                    //清空地图上的多边形
                    for (let i = 0; i < this.shapes.length; i++) {
                        this.map.removeOverlay(this.shapes[i]);
                    }
                    this.shapeDatas = res.data.data;

                    //显示多边形
                    this.shapes = [];
                    for (let i = 0; i < this.shapeDatas.length; i++) {
                        this.shapes.push(null);
                        this.showOneShape(this.shapeDatas[i].points, i);
                    }
                    //生成立体层
                    this.showShapeLayer();
                    if (this.mapType == "2D") {
                        //隐藏立体层
                        this.shapeLayerView.hide();
                    }
                }
            });
        },
        getSiteMapEquipData() {
            getSiteMapEquip({ SITEID: this.$route.params.id }).then((res) => {
                if (res.success) {
                    //清空地图上的标注
                    for (let i = 0; i < this.equips.length; i++) {
                        this.map.removeOverlay(this.equips[i]);
                    }
                    let equipAll = res.data;
                    //显示标注
                    this.equips = [];
                    this.equipDatas = [];
                    this.equipUnuse = [];
                    let dataindex = 0;
                    for (let i = 0; i < equipAll.length; i++) {
                        //保存编辑前数据，以便取消编辑的时候恢复数据
                        equipAll[i]["equipOldHeight"] = equipAll[i].equipheight;

                        if (equipAll[i].SMEID) {
                            //已经打过点的设备
                            this.equips.push(null);
                            //保存编辑前数据，以便取消编辑的时候恢复数据
                            let equipTypeCode = [];
                            equipTypeCode.push(equipAll[i].equiptype);
                            equipTypeCode.push(equipAll[i].equipcode);
                            equipAll[i].equiptypecode = equipTypeCode;
                            let equipLngLat = [];
                            equipLngLat.push(equipAll[i].equiplng);
                            equipLngLat.push(equipAll[i].equiplat);
                            equipAll[i].equipLngLat = equipLngLat;

                            this.equipDatas.push(equipAll[i]);
                            this.showOneEquip(
                                equipAll[i].equiplng,
                                equipAll[i].equiplat,
                                equipAll[i].equiptype,
                                dataindex
                            );
                            dataindex++;
                        } else {
                            //没有打过点的设备
                            let equiptype = equipAll[i].equiptype;
                            let opt = this.equipUnuse.filter(
                                (item) => item.value === equiptype
                            );
                            if (opt.length > 0) {
                                opt[0].children.push({
                                    value: equipAll[i].equipcode,
                                    label: equipAll[i].equipcode,
                                });
                            } else {
                                console.log("aa");
                                console.log(equipAll[i].equiptype);
                                this.equipUnuse.push({
                                    value: equipAll[i].equiptype,
                                    label: this.mEquipType[
                                        equipAll[i].equiptype
                                    ],
                                    children: [
                                        {
                                            value: equipAll[i].equipcode,
                                            label: equipAll[i].equipcode,
                                        },
                                    ],
                                });
                            }
                        }
                    }
                }
            });
        },
        selectShape(index) {
            for (let i = 0; i < this.shapes.length; i++) {
                if (index == i) {
                    this.shapes[i].setStrokeColor("#ffe600");
                } else {
                    this.shapes[i].setStrokeColor("#f800cf");
                }
            }
        },
        selectEquip(index) {
            if (this.equipDatas[index].equiplng) {
                let point = new BMapGL.Point(
                    this.equipDatas[index].equiplng,
                    this.equipDatas[index].equiplat
                );
                this.map.setCenter(point);
            }
        },
        loadMap() {
            // 传入密钥获取地图回调。
            BMPGL(this.ak)
                .then((BMapGL) => {
                    let that = this;
                    // 创建地图实例
                    let map = new BMapGL.Map("mapGl");
                    let point = new BMapGL.Point(this.sitelng, this.sitelat);
                    map.centerAndZoom(point, 18);
                    map.enableScrollWheelZoom(true);
                    // map.disableDoubleClickZoom();
                    map.setDefaultCursor("pointer");
                    map.setMaxZoom(20);
                    map.setMapStyleV2({
                        styleId: "122b977cdd3fdde461a0db4947e30d61",
                    });
                    map.setDisplayOptions({
                        skyColors: ["rgba(0, 0, 0, 0)", "rgba(0, 0, 0, 0.3)"],
                    });
                    //添加控件到地图中
                    var myZoomCtrl = this.createMapCtrl(map);
                    map.addControl(myZoomCtrl);
                    map.setDisplayOptions({
                        poiText: true, // 隐藏poi标注
                        poiIcon: false, // 隐藏poi图标
                        building: false, // 隐藏楼块
                    });

                    map.addEventListener("click", function (e) {
                        if (!e.overlay) {
                            if (that.tabType == "plane") {
                                //平面图tab选中时，map的点击处理
                                that.clickIndex++;

                                if (that.clickIndex % 2 == 1) {
                                    //开始点
                                    map.clearOverlays();
                                    that.mapData.ltlng = e.latlng.lng;
                                    that.mapData.ltlat = e.latlng.lat;
                                    that.setStEdMarker(1);
                                } else {
                                    //结束点
                                    that.clickIndex = 0;
                                    that.mapData.rblng = e.latlng.lng;
                                    that.mapData.rblat = e.latlng.lat;
                                    that.setStEdMarker(2);
                                    that.showPlane();
                                }
                            } else if (that.tabType == "shape") {
                                if (!that.isEditShape) {
                                    return;
                                }
                                if (that.editShapeIndex < 0) {
                                    return;
                                }
                                console.log("点击多边形");
                                let points = JSON.parse(
                                    that.shapeDatas[that.editShapeIndex].points
                                );
                                points.push([e.latlng.lng, e.latlng.lat]);
                                // if (pointsstr.length > 0) {
                                //     that.shapeDatas[
                                //         that.editShapeIndex
                                //     ].points = `${pointsstr},[${e.latlng.lng},${e.latlng.lat}]`;
                                // } else {
                                //     that.shapeDatas[
                                //         that.editShapeIndex
                                //     ].points = `[${e.latlng.lng},${e.latlng.lat}]`;
                                // }
                                that.shapeDatas[that.editShapeIndex].points =
                                    JSON.stringify(points);
                                that.showOneShape(
                                    that.shapeDatas[that.editShapeIndex].points,
                                    that.editShapeIndex
                                );
                            } else if (that.tabType == "equip") {
                                if (!that.isEditEquip) {
                                    return;
                                }
                                if (that.editShapeIndex < 0) {
                                    return;
                                }

                                that.equipDatas[that.editShapeIndex].equiplng =
                                    e.latlng.lng;
                                that.equipDatas[that.editShapeIndex].equiplat =
                                    e.latlng.lat;
