        },
        createMapCtrl(map) {
            let that = this;
            //定义一个控件类
            function MapControl() {
                this.defaultAnchor = BMAP_ANCHOR_BOTTOM_RIGHT;
                this.defaultOffset = new BMapGL.Size(20, 20);
            }
            //通过JavaScript的prototype属性继承于BMap.Control
            MapControl.prototype = new BMapGL.Control();

            MapControl.prototype.initialize = function (map) {
                //创建一个dom元素
                var div = document.createElement("div");
                //添加文字说明
                div.appendChild(document.createTextNode("2D"));
                // 设置样式
                div.style.cursor = "pointer";
                div.style.padding = "7px 10px";
                div.style.boxShadow = "0 2px 6px 0 rgba(27, 142, 236, 0.2)";
                div.style.borderRadius = "5px";
                div.style.backgroundColor = "white";
                // 绑定事件
                div.onclick = (e) => {
                    if (that.mapType == "2D") {
                        //切换成3D
                        div.innerHTML = "3D";
                        that.mapType = "3D";
                        map.setTilt(60);
                        if (this.tabType != "plane") {
                            that.shapeLayerView.show();
                        }
                    } else {
                        //切换成2D
                        div.innerHTML = "2D";
                        that.mapType = "2D";
                        map.setTilt(0);
                        that.shapeLayerView.hide();
                    }
                };
                // 添加DOM元素到地图中
                map.getContainer().appendChild(div);
                // 将DOM元素返回
                return div;
            };
            //创建控件元素
            var myZoomCtrl = new MapControl();
            return myZoomCtrl;
        },
        setStEdMarker(type) {
            var that = this;
            if (!this.mapData) {
                return;
            }
            if (type == "1") {
                //开始点
                var marker = new BMapGL.Marker(
                    new BMapGL.Point(this.mapData.ltlng, this.mapData.ltlat),
                    {
                        enableDragging: true,
                    }
                );
                marker.addEventListener("dragend", function () {
                    var nowPoint = marker.getPosition(); // 拖拽完成之后坐标的位置

                    that.mapData.ltlng = marker.getPosition().lng;
                    that.mapData.ltlat = marker.getPosition().lat;
                    console.log("移动开始点");

                    that.showPlane();
                });
                this.stMarker = marker;
                this.map.addOverlay(marker);
            } else {
                //结束点
                var marker = new BMapGL.Marker(
                    new BMapGL.Point(this.mapData.rblng, this.mapData.rblat),
                    {
                        enableDragging: true,
                    }
                );
                marker.addEventListener("dragend", function () {
                    var nowPoint = marker.getPosition(); // 拖拽完成之后坐标的位置

                    that.mapData.rblng = marker.getPosition().lng;
                    that.mapData.rblat = marker.getPosition().lat;
                    console.log("移动结束点");

                    that.showPlane();
                });

                this.edMarker = marker;
                this.map.addOverlay(marker);
            }
        },
        showShapeLayer() {
            this.shapeLayerData = [];
            for (let i = 0; i < this.shapeDatas.length; i++) {
                var coordinates = [];
                coordinates.push(JSON.parse(this.shapeDatas[i].points));
                this.shapeLayerData.push({
                    geometry: {
                        type: "Polygon",
                        coordinates: coordinates,
                    },
                    properties: {
                        height: this.shapeDatas[i].shapeheight, // 多边形高度
                    },
                });
            }
            this.shapeLayer.setData(this.shapeLayerData);
        },
        showOneShape(data, index) {
            let mapPoints = [];
            let points = JSON.parse(data);
            for (let i = 0; i < points.length; i++) {
                mapPoints.push(new BMapGL.Point(points[i][0], points[i][1]));
            }

            if (this.shapes[index]) {
                this.map.removeOverlay(this.shapes[index]);
            }

            this.shapes[index] = new BMapGL.Polygon(mapPoints, {
                strokeColor: "#f800cf",
                strokeWeight: 2,
                strokeOpacity: 0.9,
                fillColor: "#f800cf",
                fillOpacity: 0.2,
            });
            this.map.addOverlay(this.shapes[index]);
        },
        showOneEquip(lng, lat, type, index) {
            if (type <= 0) {
                return;
            }
            if (this.equips[index]) {
                //移除旧的点
                console.log("移除旧的点");
                this.map.removeOverlay(this.equips[index]);
            }
            // 将点添加到地图上
            var point = new BMapGL.Point(lng, lat);
            // 创建贴图纹理Icon
            //【旧】 1:扬尘设备;2:摄像头;3:雾泡;4:大屏;5:特种设备6:区域定位7:楼宇定位
            //【新】 1 扬尘，2 摄像头，3 塔吊，4 升降机，5 卸料平台，6 深基坑，7 高支模，8 临边防护,9 实名制 10临边围挡(旧)
            // let offsetx = (type - 1) * 50;
            const imgWidth = 45;
            const imgHeight = 46;
            let offsetx = (type - 1) * (imgWidth + 1);
            var icon = new BMapGL.Icon(
                markerImg,
                new BMapGL.Size(imgWidth + 1, imgHeight),
                {
                    anchor: new BMapGL.Size(imgWidth / 2, imgHeight),
                    imageOffset: new BMapGL.Size(offsetx, 0),
                }
            );
            icon.setImageSize(new BMapGL.Size(1000, 46));

            this.equips[index] = new BMapGL.Marker3D(
                point,
                this.equipDatas[index].equipheight,
                {
                    size: 40,
                    // shape: "BMAP_SHAPE_CIRCLE",
                    // fillColor: "#454399",
                    // fillOpacity: 0.6,
                    icon: icon,
                }
            );
            // var marker = new BMapGL.Marker3D(point, 0, {
            //     size: 50,
            //     icon: icon,
            // });
            // marker.setOffset(new BMapGL.Size(50, 25));

            this.map.addOverlay(this.equips[index]);
            // this.map.removeOverlay(this.equips[index]);
        },
        showPlane() {
            if (!this.mapData) {
                return;
            }

            if (this.imgOverlay) {
                this.map.removeOverlay(this.imgOverlay);
            }
            var bounds = new BMapGL.Bounds(
                new BMapGL.Point(this.mapData.ltlng, this.mapData.ltlat),
                new BMapGL.Point(this.mapData.rblng, this.mapData.rblat)
            );
            if (this.planeurls.length > 0) {
                this.imgOverlay = new BMapGL.GroundOverlay(bounds, {
                    type: "image",
                    url: this.planeurls[0].url,
                    opacity: this.planeOpacity,
                });
                this.imgOverlay._config.opacity = this.planeOpacity;
                this.map.addOverlay(this.imgOverlay);
            }
        },
        tabClick(tab, event) {
            this.tabType = tab.name;
            this.isEditShape = false;
            //隐藏拖拽平面图的两个点
            this.map.removeOverlay(this.stMarker);
            this.map.removeOverlay(this.edMarker);

            if (this.tabType == "plane") {
                //平面图
                //显示拖拽平面图的两个点
                this.setStEdMarker(1);
                this.setStEdMarker(2);
                //清除多边形
                for (let i = 0; i < this.shapes.length; i++) {
                    this.map.removeOverlay(this.shapes[i]);
                }
                //清除设备点位
                for (let i = 0; i < this.equips.length; i++) {
                    this.map.removeOverlay(this.equips[i]);
                }
                //地图切换到2D模式
                this.map.setTilt(0);
                this.shapeLayerView.hide();
            } else if (this.tabType == "shape") {
                //建筑物
                //获取多边形数据
                this.getSiteMapShapeData();
                this.editShapeIndex = -1;
            } else if (this.tabType == "equip") {
                //设备标注
                //获取设备标注数据
                this.getSiteMapEquipData();
                this.editShapeIndex = -1;
            }
        },
