        editEquip(index) {
            this.editShapeIndex = index;
            this.isEditEquip = true;
            this.equipOpts = [];

            for (let i = 0; i < this.equipUnuse.length; i++) {
                this.equipOpts.push(this.equipUnuse[i]);
            }
            let equiptypecode = this.equipDatas[index].equiptypecode;

            if (equiptypecode && equiptypecode.length > 0) {
                let equiptype = this.equipDatas[index].equiptype;
                let equipcode = this.equipDatas[index].equipcode;

                // this.equipDatas[this.editShapeIndex].equipcode = [];
                // this.equipDatas[this.editShapeIndex].equipcode.push(equiptype);
                // this.equipDatas[this.editShapeIndex].equipcode.push(equipcode);
                let opt = this.equipOpts.filter(
                    (item) => item.value === equiptype
                );

                if (opt.length > 0) {
                    opt[0].children.push({
                        value: equipcode,
                        label: equipcode,
                    });
                } else {
                    this.equipOpts.push({
                        value: equiptype,
                        label: this.mEquipType[equiptype],
                        children: [
                            {
                                value: equipcode,
                                label: equipcode,
                            },
                        ],
                    });
                }
                this.equipOpts.sort((a, b) => {
                    return a.value - b.value;
                });
            }
        },
