        beforeAvatarUpload(file) {
            let isImgOverSize = file.size / 1024 / 1024 > 1;
            if (isImgOverSize) {
                this.$message.error("上传平面图大小不能超过 1MB!");
            }
            this.uploadDisabled = "disabled";

            return isImgOverSize;
