        fileUpload(param) {
            let that = this;
            let isImgOverSize = param.file.size / 1024 / 1024 > 1;
            if (isImgOverSize) {
                this.$message.error("上传平面图大小不能超过 1MB!");
                return;
            }
            var FileExt = param.file.name.replace(/.+\./, "");
            if (FileExt != "jpg") {
                this.$message.error("请上传jpg格式图片!");
                return;
            }
            let formparam = new FormData();
            formparam.append("formFile", param.file);
            formparam.append("SITEID", this.$route.params.id);
            formparam.append("GROUPID", this.mapData.GROUPID);
            let floading = this.$loading({
                lock: true,
                text: "正在上传图片...",
                spinner: "el-icon-loading",
                background: "rgba(0, 0, 0, 0.7)",
            });

            saveSiteMapPlane(formparam).then((res) => {
                floading.close();
                if (res.success) {
                    this.$message({
                        message: "图片上传成功",
                        type: "success",
                    });
                    this.getSiteMapData();
                } else {
                    this.$message({
                        message: res.msg,
                        type: "error",
                    });
                }
            });
        },
        submitUpload() {
            this.$refs.upload.submit();
        },
        changeHeight() {},
    },
    mounted() {
        this.listHeight = window.innerHeight - 450;
        this.equipListHeight = window.innerHeight - 350;
        this.getSetting();
        // this.getCitys(0);
        // this.sglx = this.$store.state.options.sglx;
    },
};
</script>
<style scoped>
.map {
    width: 100%;
    height: calc(100vh - 150px);
}
.text-regular {
    color: #606266;
}
.form-content {
    margin-top: 20px;
}
.text-base {
    font-size: 14px;
}
.shape-row {
    width: 100%;
    color: #666560;
    font-size: 14px;
    border-bottom: 1px dashed #eeeded;
    /* height: 50px;
    line-height: 50px; */
    padding: 10px;
    vertical-align: middle;
}
.shape-row:hover {
    background: #c7dfe95b;
}
.table-div {
    /* display: inline; */
    width: 100%;
    overflow: hidden;
    /* white-space: nowrap; */
    text-overflow: ellipsis;
}
.form-item {
    margin-bottom: 0;
}
.el-input-group__prepend {
    padding-left: 10px !important;
    padding-right: 10px !important;
}
.m-t-20 {
    margin-top: 20px;
}
.siteshortnamediv {
    position: absolute;
    top: 10px;
    left: 10px;
    color: #fff;
    z-index: 1;
}
</style>
<style>
.disabled .el-upload--picture-card {
    display: none;
}
</style>


